<template>
  <div class="m-container" ref="container">
    <div class="header"></div>
    <div class="main">
      <div class="sider" style="overflow: scroll">
        <router-link to="/home"><div class="item">首页</div></router-link>
        <router-link to="/bespeak"><div class="item">预约/挂号</div></router-link>
        <router-link to="/home"><div class="item">咨询</div></router-link>
        <router-link to="/home"><div class="item">介绍</div></router-link>
        <router-link to="/home"><div class="item">内容</div></router-link>
        <router-link to="/members"><div class="item">会员</div></router-link>
        <router-link to="/home"><div class="item">权限</div></router-link>
      </div>
      <div style="width: calc(100% - 150px)">
        <router-view ></router-view>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  mounted () {
    this.$refs.container.style.height = `${document.documentElement.clientHeight}px`
    window.onresize = () => {
      this.$refs.container.style.height = `${document.documentElement.clientHeight}px`
    }
  },
  data () {
    return {
      height: `${document.documentElement.clientHeight - 70}px`
    }
  }
}
</script>
<style lang="stylus" scoped rel="stylesheet/stylus">
@import "~common/stylus/base"
@import "~common/stylus/variable"
.m-container
  @extend .flex
  @extend .flex-column
  width: 100%
  .header
    width: 100%
    min-width: 1300px
    height: 70px
    background-color: $color-back-blue_0
  .main
    @extend .flex
    @extend .flex-row
    width: 100%
    height: calc(100% - 70px)
    overflow: none
    .sider
      @extend .flex
      @extend .flex-column
      width: 150px
      background-color: $color-back-black_0
      .item
        @extend .flex
        @extend .flex-row
        @extend .flex-start
        @extend .pointer
        width: 100%
        height: 50px
        padding: 0 20px
        color: $color-back-white_0
        font-size: $size-14
        font-weight: bold
</style>



